@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');


body {
  margin: 0;
  font-family: 'Poppins';
  /*font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;*/
  /*-webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;*/

  /*background-color: #50545c;*/
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*nav*/
.navBar {
  background-color: #393E46;

  display: flex;
  justify-content: space-between;

  padding: 20px;
}

.logoContainer {
  font-size: 36px;
  font-weight: 600;

  margin-top: auto;
  margin-bottom: auto;

  color: #00ADB5;
}

.linkContainer {
  margin-top: auto;
  margin-bottom: auto;
}

.btnContainer {
  display: flex;

  margin-top: auto;
  margin-bottom: auto;
}

#homeBtn, #myPostsBtn, #addBtn {
  color: #EEEEEE;

  text-decoration: none;
  font-size: 15px;

  margin: 10px;
}

.signupBtn, .signinBtn {
  background-color: #00ADB5;
  color: #393E46;
  
  text-decoration: none;

  border: solid;
  border-radius: 25px;
  border-color: #00ADB5;

  font-size: 15px;
  font-weight: 600;

  border-radius: 25px;

  padding: 6px 12px 6px 12px;
  margin: 10px;
}

.signinBtn {
  color: #00ADB5;

  background: none;

  border: solid;
  border-radius: 25px;
}

.signoutBtnContainer {
  position: absolute; 

  bottom: 80px;
}

.signoutBtn {
  color: #E26868;

  background: none;

  border: solid;
  border-radius: 25px;

  text-decoration: none;
  font-size: 15px;

  border-radius: 25px;

  padding: 8px 16px 8px 16px;
  margin: 10px;
}
.signUpFormBtnContainer, .signInFormBtnContainer {
  display: flex;
  justify-content: space-around;
}

.formBtn {
  background-color: #00ADB5;
  color: #EEEEEE;

  padding: 6px 12px 6px 12px;

  border-radius: 5px;

  font-size: 18px;

  border: none;
}

/*card*/
.profile-username {
  display: flex;

  padding-bottom: 10px;
}

.cardUsername {
  font-size: 14px;
  font-weight: 300;

  margin-top: auto;
  margin-bottom: auto;
  margin-left: 10px;
}

.content-card-title {
  font-size: 20px;
  font-weight: 600;
}

.content {
  padding: 10px 0px 10px 0px;
  
  font-weight: 400;
  font-size: 14px;
}

.cardBtn {
  display: flex;
  justify-content: space-between;
}

.cardFooter {
  display: flex;
}

.likeBtn, .likeCounter, .dislikeBtn {
  margin-top: auto;
  margin-bottom: auto;

  padding: 0px 8px 0px 8px;
}

.deleteBtn {
  background-color: #E26868;
  padding: 6px 18px 6px 18px;

  border-radius: 5px;

  text-align: center;
}

.editBtn {
  background-color: #00ABB3;
  padding: 6px 18px 6px 18px;

  border-radius: 5px;

  text-align: center;
}

.editFormBtnContainer {
  display: flex;
  justify-content: space-between;
}

.formEditBtn {
  text-align: center;

  padding: 10px 20px 10px 20px;

  width: 30%;

  border: none;
  border-radius: 4px;
  
  background-color: #00ABB3;
  color: #EEEEEE;

  font-size: 15px;

  cursor: pointer;
}


.content-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.content-card {
  background-color: #50545c;
  color: #EEEEEE;

  border-radius: 10px;

  padding: 20px;
  margin: 20px;  
}

/*add*/
input[type=text], input[type=password], input[type=email] {
  color: #50545c;

  display: inline-block;

  width: 100%;
  padding: 12px 20px;
  margin: 10px 0px 15px 0px;
  
  border: 2px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  outline: none;

  font-size: 15px;

  transition: 0.5s;
}

label {
  color: #00ADB5;

  font-weight: 400;
  font-size: 18px;
}

input[type=text]:focus, input[type=password]:focus, input[type=email]:focus {
  color: #3C4048;

  border: 2px solid #50545c;

  font-size: 15px;
}

.alreadySignedUp, .dontHaveAccount {
  display: flex;

  color: #393E46;
}

.signInAgain, .signUpFirstTime {
  color: #00ADB5;

  text-decoration: none;
}

.mainAddBtn {
  position: fixed;

  right: 10%;
  bottom: 10%;

  font-size: 20px;

  padding: 10px 20px 10px 20px;

  border-radius: 10px;

  color: #EEEEEE;
  background-color: #00ABB3;

  cursor: pointer;
}

.popupBg {
  position: fixed;

  left: 0;
  top: 0;

  width: 100%;
  height: 100vh;

  background-color: rgba(0, 0, 0, 0.5);
}

.popupBox {
  position: relative;

  padding: 30px;

  left: 50%;
  top: 15%;

  transform: translate(-50%, 0);

  width: 50%;

  border-radius: 10px;

  background-color: #EEEEEE;
  color: #3C4048;
}

.formBtnContainer {
  display: flex;
  justify-content: space-between;
}

.formAddBtn, .cancelBtn {
  text-align: center;

  padding: 10px 20px 10px 20px;

  width: 30%;

  border: none;
  border-radius: 4px;
  
  color: #EEEEEE;

  font-size: 15px;

  cursor: pointer;
}

.formAddBtn {
  background-color: #00ABB3;
}

.cancelBtn {
  background-color: #E26868;
}

.signupContainer, .signinContainer {
  color: #3C4048;

  width: 30%;

  padding: 20px;
  margin-left: 60%;
}

.errorPopup {
  position: fixed;

  background-color: #E26868;
  color: #EEEEEE;

  border-radius: 5px;

  padding: 10px;

  transform: translate(-50%, 0);

  left: 0;
  bottom: 40px;

  margin-left: 50%;
}

.passwordRestrains {
  display: flex;

  align-items: center;

  padding: 5px 0px 5px 0px;

  font-size: 15px;
}

.customRadioGray {
  height: 15px;
  width: 15px;

  margin-right: 10px;

  background-color: #bbb;
  border-radius: 50%;
  
  display: inline-block;
}

.customRadioGreen {
  height: 15px;
  width: 15px;

  margin-right: 10px;

  background-color: #03C988;

  border-radius: 50%;
  
  display: inline-block;
}

.sidePanelBg {
  position: fixed;

  left: 0;
  top: 0;

  width: 100%;
  height: 100vh;

  background-color: rgba(0, 0, 0, 0.5);

  display: grid;
  grid-template-columns: 60% 40%;

  z-index: 10;
}

.sidePanelBg2 {
  height: 100vh;

  background-color: rgba(0, 0, 0, 0.5);
}

.sidePanelBox {
  position: relative;

  padding: 30px;

  height: 100vh;

  border-radius: 10px;

  background-color: #EEEEEE;
  color: #3C4048;
}

.profileContainer {
  display: grid;
  grid-template-columns: 20% 80%;

  background-color: #CCCCCC;

  border-radius: 10px;

  padding: 10px;
}

.profileGrid1 {
  margin: auto 0 auto 0;
}

.profileGrid2 {
  margin: auto 0 auto 20px;
}

.profileUsername {
  font-size: 20px;
  font-weight: 400;
}

.profileEmail {
  font-size: 15px;
  font-weight: 300;
}

@media only screen and (max-width: 900px) {
  .content-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }

  .signupBtn, .signinBtn {
    font-size: 12px;
  
    border-radius: 25px;
  
    padding: 4px 12px 4px 12px;
    margin: 6px;
  }
}

@media only screen and (max-width: 600px) {
  .content-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .signupBtn, .signinBtn {
    display: block;
  }

  .navBarMiddle {
    /*display: block;*/

    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }

  .signupContainer, .signinContainer {
    width: 80%;

    margin: auto;
  }

  .logoContainer {
    font-size: 20px;
  }
}

@media only screen and (max-width: 300px) {
  .content-grid {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
}

